  /**
   * 跳转到个股页面
   * @param  {[type]} stockcode [description]
   * @return {[type]}           [description]
   */
  AG.gotoGgpage = function (stockcode) {
    var ary = ["SZ", "SH", "SF", "BI","BK", "SO", "FO"]
    // stockcode=stockcode.replace(/BK/i,'BI')
    if(stockcode.indexOf('.')>=0){
      stockcode = stockcode.replace(/(\d+)\.([a-zA-Z]+)/, function (m, n, o) {
        return o+n
      })  
    }
    stockcode = stockcode.replace(/([a-zA-Z]+)(\d+)/, function (m, n, o) {
      if (ary.indexOf(n.toUpperCase()) >= 0) return m
      return n + o // + encodeURIComponent("|") 
    })

    if (!stockcode) return 0
    if (AG.ISIOS || AG.ISWP) {
      global.iosRequest("eastmoney://em.dpl/page/stockdetail/" + stockcode)
    } else if (AG.ISAndroid) {
      global.androidRequest("dfcft://stock?stockcode=" + stockcode)
    } else return 0
    return 1
  }


